import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { apiservice } from "../Api/api";
import showNotification from "../showNotification";
import "./singlePost.css";
import Loader from "../Loading/Loading";
export default function SinglePost() {
  const { id } = useParams()
  const [ loading , setLoading ] = useState(false)
  const [ data , setData ] = useState({})
  useEffect(()=>{
    if (JSON.stringify(data) === JSON.stringify({}))
    {
      getValues()
    }
    else
    {
      return
    }
  },[data])
  const getValues = async () => {
    try {
      setLoading(true)
      const response = await apiservice.getBlogPost(id)
      setLoading(false)
      if (response)
      {
        setData(response)
        showNotification("Successful","success")
      }
    }
    catch (error)
    {
      setLoading(false)
      console.log(error)
      showNotification("Single post error","error")
    }
  }
  const datefunction = (date) => {
    const postedDate = new Date(date)
    const nowDate = new Date()
    var hours = Math.abs(postedDate - nowDate) / 36e5;
    return `${Math.ceil(hours)} hours ago`
  }
  return (
    <div className="singlePost">
      <div className="singlePostWrapper">
        <img
          className="singlePostImg"
          src={data?.urlToImage}
          alt=""
        />
        <h1 className="singlePostTitle">
          {data?.title}
        </h1>
        <div className="singlePostInfo">
          <span>
            Author:
            <b className="singlePostAuthor">
              {data?.author}
            </b>
          </span>
          <span>{datefunction(data?.publishedAt)}</span>
        </div>
        <p className="singlePostDesc" style={{textAlign : "justify"}}>
          {data?.description}
          <br />
          <br />
          {data?.content}
        </p>
      </div>
      {loading && <Loader/>}
    </div>
  );
}
