import { Link } from "react-router-dom";
import "./post.css";

export default function Post({data , index , type }) {
  const datefunction = (date) => {
    const postedDate = new Date(date)
    const nowDate = new Date()
    var hours = Math.abs(postedDate - nowDate) / 36e5;
    return `${Math.ceil(hours)} hours ago`
  }
  return (
    <div className="post">
      { type === "blogs" ? 
      <img
        className="postImg"
        src={data?.urlToImage}
        alt=""
        
      /> : <a href={data?.url} target="_blank" rel="noopener noreferrer">
      <img
        className="postImg"
        src={data?.urlToImage}
        alt=""
      />
      </a>
      }
      
      <div className="postInfo">
        <span className="postTitle" style={{textAlign  : "center"}}>
          { type === "blogs" ? 
            <Link to={`/post/${data?.id}`} className="link">
            {data?.title}
            </Link> : data?.title 
          }
          
        </span>
        <hr />
        <span className="postDate">{datefunction(data?.publishedAt)}</span>
      </div>
      <p className="postDesc" style={{textAlign  : "center"}}>
        {data?.description}
      </p>
    </div>
  );
}
