import { useEffect , useState } from "react";
import { apiservice } from "../Api/api";
import Post from "../post/Post";
import showNotification from "../showNotification";
import "./Blogs.css";
import Loader from "../Loading/Loading";

const Blogs = ({ user , setUser }) => {
    const [loading , setLoading] = useState(false)
    const [ posts , setPosts ] = useState([])
    useEffect(()=>{
        if (posts.length === 0)
        {
          getPosts()
        }
        else
        {
          return
        }
    },[posts])

    const getPosts = async () => {
        try {
            setLoading(true)
            const response = await apiservice.getBlogs()
            setLoading(false)
            if (response)
            {
              showNotification("Successfully fetched Data" , "success")
              setPosts(response?.articles)
            }
        } catch (error) {
          setLoading(false)
          console.log(error)
          showNotification("Couldn't fetch","error")
        }
    }
    return (
        <div className="posts">
        {
          posts?.map((post,index)=> {
            return (
              <Post data={post} key={index} index={index} type={"blogs"}/>
            )
          })
        }
        {
          posts?.length === 0 && (
            <h1 style={{textAlign : "center" , marginTop : "50px" , width : "100%"}}>No Posts to show</h1>
          )
        }
        {loading && <Loader/>}
      </div>
    )
}

export default Blogs