import Topbar from "./components/topbar/Topbar";
import Homepage from "./pages/homepage/Homepage";
import Single from "./components/single/Single";
import Write from "./components/write/Write";
import { BrowserRouter as Router, Switch, Route, useHistory } from "react-router-dom";
import { useEffect, useState } from "react";
import Posts from "./components/posts/Posts";
import Blogs  from "./components/Blogs/Blogs";
function Default () {
  const history = useHistory()
  history.push('/home')
  window.location.reload()
  return
}

function App() {
  const [ user , setUser ] = useState(false)
  useEffect(()=>{
    if (sessionStorage.getItem("User") && sessionStorage.getItem("Expiry"))
    {
      const expiry = sessionStorage.getItem("Expiry")
      const time = new Date().getTime()
      const expiryseconds = new Date(expiry).getTime()
      if (time > expiryseconds )
      {
        setUser(false)
        sessionStorage.removeItem("User")
        sessionStorage.removeItem("Expiry")
        return
      }
      setUser(true)
    }
    else
    {
      setUser(false)
    }
  },[user])
  return (
    <Router>
      <Topbar user={user} setUser={setUser} />
      <Switch>
        <Route exact path="/">
          <Default />
        </Route>
        <Route exact path="/home">
          <Homepage user={user} setUser={setUser} />
        </Route>
        <Route path="/news" exact>
          <Posts user={user} setUser={setUser}/>
        </Route>
        <Route path="/blogs" exact>
          <Blogs user={user} setUser={setUser}/>
        </Route>
        <Route path="/post/:id">
          <Single user={user} setUser={setUser} />
        </Route>
        <Route path="/write"><Write user={user} setUser={setUser}/></Route>
      </Switch>
    </Router>
  );
}

export default App;
